                          label="Set Accounts as Dormant"
                          label-for="autodormant"
                          id="autodormant"
                          v-model="form.autoDormant"
                        />
                      </div>
                    </div>
                    <div class="col-6" v-if="form.autoDormant">
                      <div class="form__item">
                        <BaseInput
                          label="After"
                          v-model="form.autoDormantAfter"
                          type="number"
                          placeholder="After"
                        />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <div class="modal__accordion">
              <li
                data-toggle="collapse"
                class="sidebar-right__item modal__accordion--toggle"
                data-target="#productFees"
                aria-expanded="false"
                aria-controls="productFees"
              >
                <h5
                  class="w-100 mb-0 d-flex align-items-center justify-content-between"
                >
                  product fees
                  <ion-icon name="chevron-down-outline"></ion-icon>
                </h5>
              </li>
              <div id="productFees" class="collapse" data-parent="#accordion">
                <div class="pt-5">
                  <div class="form__item">
                    <BaseCheckbox
                      label="Allow Arbitrary Fees"
                      label-for=""
                      id="allowArbitraryFees"
                      v-model="form.allowArbitraryFees"
                    />
                  </div>
                  <div class="form__item">
                    <BaseCheckbox
                      label="Show Inactive Fees"
                      label-for=""
                      id="showInactiveFees"
                      v-model="form.showInactiveFees"
                    />
                  </div>
                  <div class="form__item">
                    <button
                      type="button"
                      class="button page__body__header__button button--grey text--capital"
                      @click="addFee"
                    >
                      <ion-icon
                        name="add-outline"
                        class="text--regular"
                      ></ion-icon>
                      <span class="d-inline-flex ml-2">add fees</span>
                    </button>
                  </div>
                  <div
                    class="form__item p-5 border border-radius"
                    v-for="(fee, counter) in form.fees"
                    :key="counter"
                  >
                    <div class="row">
                      <div class="col-6">
                        <BaseInput
                          label="Name"
                          v-model="form.fees.feeName"
                          type="text"
                          placeholder="Enter name"
                        />
                      </div>
                      <div class="col-6">
                        <div class="form__item form__item--label">
                          <label class="form__label">Type</label>
                          <select
                            class="select select--lg"
                            required
                            v-model="form.fees.feeType"
                            @change="feeChange"
                          >
                            <option value="" disabled selected
                              >Select type</option
                            >
                            <option
                              :key="feeTypes.id"
                              v-for="feeTypes in allFeeTypes.data"
                            >
                              {{ feeTypes }}
                            </option>
                          </select>
                        </div>
                      </div>
                    </div>
                    <div class="row">
                      <div class="col-6" v-show="feeMonthly">
                        <div class="form__item form__item--label">
                          <label class="form__label">fee payment</label>
                          <select
                            class="select select--lg"
                            required
                            v-model="form.fees.feePayment"
                          >
                            <option value="" disabled selected
                              >Select fee payment</option
                            >
                            <option
                              :key="appliedDates.id"
                              v-for="appliedDates in allAppliedDates.data"
                            >
                              {{ appliedDates }}
                            </option>
                          </select>
                        </div>
                      </div>
                      <div class="col-6">
                        <BaseInput
                          label="Amount"
                          type="text"
                          placeholder="Enter amount"
                          v-model="form.fees.fee"
                        />
                      </div>
                    </div>
                    <button
                      type="button"
                      class="button ml-auto page__body__header__button button--cancel text--capital"
                      @click="deleteFee(counter)"
                    >
                      <span class="d-inline-flex">delete</span>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </ul>

          <div class="form__item">
            <div class="form__action">
              <button
                class="button form__button form__button--lg"
                @click.prevent="onSubmit"
              >
                Save changes
              </button>
              <a
                role="button"
                class="modal__close form__action__text form__action__text--danger text--capital ml6"
                @click.prevent="cancel"
              >
                cancel
              </a>
            </div>
          </div>
        </form>
      </template>
    </EditModal>

  </main>
</template>

<script>
import SideModal from "@/components/SideModal/_base-sideModal.vue";
import EditModal from "@/components/SideModal/_base-editSideModal.vue";
import DeleteModal from "@/components/DeleteModal/_base-delete-modal.vue";
import TextArea from "@/components/Inputs/_base-text-area.vue";
import BaseInput from "@/components/Inputs/_base-Input.vue";
import BaseCheckbox from "@/components/Inputs/_base-checkbox.vue";
import { mapGetters, mapActions } from "vuex";
import {
  CREATE_PRODUCT,
  GET_ALL_DEPOSITS,
  GET_DEPOSIT_PRODUCT
} from "@/core/services/store/deposit.module";

export default {
  name: "Deposit",
  data: function() {
    return {
      content_header: {
        title: "Deposit",
        subTitle:
          "Manage the products offerings that you have available to your customers by creating and editing loan."
      },
      form: {
        productName: "",
        productDescription: "",
        productID: "",
        productCategory: "",
        productType: "",
        productState: "",
        availability: [],
        idType: "",
        idTemplate: "@@@###",
        currency: "",
        interestRateTerms: "",
        interestPaidIntoAccount: "",
        interestChargeMethod: "",
        minInterestRateConstraints: "",
        defaultInterestRateConstraints: "",
        maxInterestRateConstraints: "",
        accountCalculation: "",
        interestDaysInYears: "",
        daysInYears: "",
        interestPaid: "",
        maxWithdrawalAmount: "",
        minDepositAmount: "",
        allowArbitraryFees: "",
        startingFrom: "",
        defaultTermLength: "",
        minTermLength: "",
        maxTermLength: "",
        termUnit: "",
        autoDormant: false,
        autoDormantAfter: "",
        defaultOpeningBalance: "",
        minOpeningBalance: "",
        maxOpeningBalance: "",
        fees: [
          {
            feeName: "",
            feeType: "",
            feePayment: "",
            fee: ""
          }
        ],
        showInactiveFees: "",
        dateMethod: "",
        createdBy: "",
        modifiedBy: "",
        months: "",
        balanceTiers: [
          {
            fromTier: Number,
            toTier: Number,
            interestTierAmount: Number
          }
        ],
        periodTiers: [
          {
            fromTier: Number,
            toTier: Number,
            interestTierAmount: Number
          }
        ]
      },
      monthly: false,
      fixedTerm: false,
      balancedTier: false,
      periodTiered: false,
      deactivatedProduct: "",
      AccountSetting: false,
      template: false,
      feeMonthly: false,
      showEditModal: false
    };
  },
  components: {
    SideModal,
    EditModal,
    DeleteModal,
    TextArea,
    BaseInput,
    BaseCheckbox
  },
  methods: {
    ...mapActions([
      "GET_ALL_PRODUCT_TYPE",
      "GET_ALL_PRODUCT_CATEGORY",
      "GET_ALL_CURRENCY",
      "GET_ALL_INTEREST_CHARGE",
      "GET_ALL_INTEREST_RATE",
      "GET_ALL_ID",
      "GET_DAYS_IN_YEARS",
      "GET_ACCOUNT_BALANCE_CALCULATION",
      "GET_ACCOUNT_INTEREST",
      "GET_MONTHS",
      "GET_FEE_TYPE",
      "GET_APPLIED_DATE_METHOD",
      "CREATE_PRODUCT",
      "GET_PRODUCT_AVAILABILITY"
    ]),
    interestHandler() {
      if (this.form.interestRateTerms === "FIXED") {
        this.fixedTerm = true;
        this.balancedTier = false;
        this.periodTiered = false;
      } else if (this.form.interestRateTerms === "TIERED PER BALANCE") {
        this.fixedTerm = false;
        this.balancedTier = true;
        this.periodTiered = false;
      } else if (this.form.interestRateTerms === "TIERED PER PERIOD") {
        this.fixedTerm = false;
        this.balancedTier = false;
        this.periodTiered = true;
      } else {
        this.fixedTerm = false;
        this.balancedTier = false;
        this.periodTiered = false;
      }
    },
    newAccountSettings() {
      if (this.form.idType === "INCREMENTAL") {
        this.AccountSetting = true;
        this.template = false;
      } else if (this.form.idType === "RANDOM") {
        this.template = true;
        this.AccountSetting = false;
      } else {
        this.template = false;
        this.AccountSetting = false;
      }
    },
    feeChange() {
      if (this.form.fees.feeType === "MonthlyFee") {
        this.feeMonthly = true;
      } else {
        this.feeMonthly = false;
      }
    },
    handleChange() {
      if (this.form.interestDaysInYears === "EveryMonth") {
        this.monthly = true;
      } else {
        this.monthly = false;
      }
    },
    addFee() {
      this.form.fees.push({
        name: "",
        type: "",
        feePayment: "",
        amount: ""
      });
    },
    deleteFee(counter) {
      this.form.fees.splice(counter, 1);
    },
    onSubmit() {
      // this.$v.form.$touch();
      // if (this.$v.form.$anyError) {
      //     return;
      // }
      let productName = this.form.productName;
      let productDescription = this.form.productDescription;
      let productID = this.form.productID;
      let productCategory = this.form.productCategory;
      let productType = this.form.productType;
      let productState = this.form.productState;
      let availability = this.form.availability;
      let idType = this.form.idType;
      let idTemplate = this.form.idTemplate;
      let currency = this.form.currency;
      let interestRateTerms = this.form.interestRateTerms;
      let interestPaidIntoAccount = this.form.interestPaidIntoAccount;
      let interestChargeMethod = this.form.interestChargeMethod;
      let minInterestRateConstraints = this.form.minInterestRateConstraints;
      let defaultInterestRateConstraints = this.form.defaultInterestRateConstraints;
      let maxInterestRateConstraints = this.form.maxInterestRateConstraints;
      let calculationAccountBalance = this.form.accountCalculation;
      let interestDaysInYears = this.form.interestDaysInYears;
      let maxWithdrawalAmount = this.form.maxWithdrawalAmount;
      let minDepositAmount = this.form.minDepositAmount;
      let allowArbitraryFees = this.form.allowArbitraryFees;
      let startingFrom = this.form.startingFrom;
      let defaultTermLength = this.form.defaultTermLength;
      let minTermLength = this.form.minTermLength;
      let maxTermLength = this.form.maxTermLength;
      let termUnit = this.form.termUnit;
      let autoDormant = this.form.autoDormant;
      let autoDormantAfter = this.form.autoDormantAfter;
      let defaultOpeningBalance = this.form.defaultOpeningBalance;
      let minOpeningBalance = this.form.minOpeningBalance;
      let maxOpeningBalance = this.form.maxOpeningBalance;
      let feeName = this.form.fees.feeName;
      let feeType = this.form.fees.feeType;
      let feePayment = this.form.fees.feePayment;
      let fee = this.form.fees.fee;
      let months = this.form.months;
      let showInactiveFees = this.form.showInactiveFees;
      let createdBy = "Jude";
      let modifiedBy = "Jude";
      // console.log(data)
      this.$store
        .dispatch(CREATE_PRODUCT, { 
          productName,
          productDescription,
          productID,
          productCategory,
          productType,
          productState,
          availability,
          idType,
          idTemplate,
          currency,
          interestRateTerms,
          interestPaidIntoAccount,
          interestChargeMethod,
          minInterestRateConstraints,
          defaultInterestRateConstraints,
          maxInterestRateConstraints,
          calculationAccountBalance,
          interestDaysInYears,
          maxWithdrawalAmount,
          minDepositAmount,
          allowArbitraryFees,
          startingFrom,
          defaultTermLength,
          minTermLength,
          maxTermLength,
          termUnit,
          autoDormant,
          autoDormantAfter,
          defaultOpeningBalance,
          minOpeningBalance,
          maxOpeningBalance,
          feeName,
          feeType,
          feePayment,
          fee,
          months,
          showInactiveFees,
          createdBy,
          modifiedBy
        })
        .then(() => this.$router.push({ name: "deposit" }));
    },
    getDeposits() {
      let index = 1;
      let size = 10;
      this.$store.dispatch(GET_ALL_DEPOSITS, {
        pageindex: index,
        pagesize: size
      })
    },
    openCreateModal() {
      this.showEditModal = true;
    },
    closeEditModal() {
      this.showEditModal = false;
    },
    getProdcutByID(id) {
      this.$store.dispatch(GET_DEPOSIT_PRODUCT, {id})
    }
  },
  computed: {
    ...mapGetters([
      "allProductTypes",
      "allproductcategory",
      "allCurrency",
      "allInterestCharge",
      "allInterestRate",
      "allIDTypes",
      "allDaysInYears",
      "allAccountBalanceCalculation",
      "allAccountInterests",
      "allMonths",
      "allFeeTypes",
      "allAppliedDates",
      "allProducts",
      "allAvailableProducts",
      "allErrors",
      "allDeposits",
      "depositProd"
    ])
  },
  created() {
    this.GET_ALL_PRODUCT_TYPE(),
      this.GET_ALL_PRODUCT_CATEGORY(),
      this.GET_ALL_CURRENCY(),
      this.GET_ALL_INTEREST_CHARGE(),
      this.GET_ALL_INTEREST_RATE(),
      this.GET_ALL_ID(),
      this.GET_DAYS_IN_YEARS(),
      this.GET_ACCOUNT_BALANCE_CALCULATION(),
      this.GET_ACCOUNT_INTEREST(),
      this.GET_MONTHS(),
      this.GET_FEE_TYPE(),
      this.GET_APPLIED_DATE_METHOD(),
      this.getDeposits(),
      this.GET_PRODUCT_AVAILABILITY();
  }
};
</script>

